import React from 'react'
import { RouteComponentProps } from 'react-router-dom'
import { Row, Col, BackTop, Affix, Form, Select, message } from 'antd'
import cx from 'classnames'
import {
  map,
  pathOr,
  keys,
  gt,
  __,
  length,
  ifElse,
  equals,
  always,
  filter,
  values,
  not,
  replace,
  split,
  join,
} from 'ramda'
import useTitle from '@/hooks/useTitle'
import { useBreadcrumb, useHeaderContent } from '@/hooks/useApp'
import {
  useReportTemplateState,
  useToggleReportPreview,
} from '@/data/useReportTemplate'
import { useReportItem } from '@/data/useReport'
import { isNil, mapIndexed, queryString } from '@/utils/webHelper'
import { SnippetProps } from '@/constants/types'
import { reportAnnotations } from '@/constants/enums'
import { Page } from '@/components/layout'
import ReactDndProvider from '@/components/snippet-scrollable-container'
import SnippetsContainer from '@/components/snippets-container'
import { Checkbox, CheckboxGroup } from '@/components/checkbox'
import Radio from '@/components/radio'
import Input from '@/components/input'
import Collapse from '@/components/collapse'
import SnippetListMenu from '@/components/snippet-list-menu'
import { renderSnippets } from '@/components/snippet-utils'
import Tabs from '@/components/draggable-tabs'
import { useReportTemplate } from '@/services/reportTemplate'
import { useAddNewReport, useUpdateReport } from '@/services/report'
import { usePathology } from '@/services/pathology'
import history from '@/utils/history'
import { REPORT_EDIT } from '@/constants/routes'
import { Anchor } from './components'
import Header from './header'
import Pie from './pie'
const { compose } = require('ramda')

const { Link } = Anchor

enum menuState {
  VISIBLE_RIGHT = '1',
  VISIBLE_LEFT = '2',
}

// interface Props extends RouteComponentProps<{ id: string }> {}

interface Props extends RouteComponentProps<{ id: string }> {
  id: any
  reviewDoctor: string
  serialNo: string
}

const col = ifElse(equals(false), always(5), always(0))

export default function ReportTyping(props: Props) {
  useTitle('')
  useBreadcrumb([{ name: '' }, { name: '' }])
  useToggleReportPreview(true)

  const [form] = Form.useForm()

  const [reportTemplates] = useReportTemplateState()

  const { data /* isValidating */ } = usePathology()

  const [visibleLayouts, setVisibleLayouts] = React.useState<string[]>([
    menuState.VISIBLE_LEFT,
    menuState.VISIBLE_RIGHT,
  ])

  //  QS  params.id  useReportItem 
  //  useReportTemplate 
  const { params } = props.match

  //  QS  params.id reportTemplate
  const qs = queryString(props.location.search)

  //  QS 
  const [templateId, setCurrentTemplateId] = React.useState(qs.templateId)

  React.useEffect(() => {
    setCurrentTemplateId(qs.templateId)
  }, [qs.templateId])

  const item = useReportItem(params.id)

  const {
    total,
    category,
    reportTemplate,
    snippetList,
    handleTabEdit,
    handleTabChange,
    handleTabMove,
    handleTabNameChange,
    handleSnippetUpdate,
  } = useReportTemplate({
    id: templateId,
    data: item,
  })

  React.useEffect(() => {
    form.setFieldsValue(reportTemplate)
  }, [JSON.stringify(reportTemplate)]) // eslint-disable-line

  React.useEffect(() => {
    if (qs.reviewDoctor) {
      form.setFieldsValue({ reviewDoctor: qs.reviewDoctor })
    }
    if (qs.serialNo) {
      form.setFieldsValue({ serialNo: qs.serialNo })
    }
  }, [JSON.stringify(qs)]) // eslint-disable-line

  const handleLayoutChange = (value: string) => {
    setVisibleLayouts((prev: string[]) =>
      prev.includes(value) ? prev.filter(x => x !== value) : [...prev, value]
    )
  }

  const leftContentVisible = visibleLayouts.includes(menuState.VISIBLE_LEFT)
  const rightContentVisible = visibleLayouts.includes(menuState.VISIBLE_RIGHT)

  const [, handleAdd] = useAddNewReport({
    onCompleted: ({ id }: any) => {
      history.push(replace(':id', id)(REPORT_EDIT))
      message.success('')
    },
  })

  const [, handleUpdate] = useUpdateReport({
    onCompleted: () => message.success(''),
  })

  const handleOk = () => {
    form.validateFields().then(values => {
      let payload = {
        ...values,
        ...(params.id ? { id: params.id } : {}),
        ...data,
        setting: snippetList,
      }

      const handler = params.id ? handleUpdate : handleAdd

      handler(payload)
    })
  }

  useHeaderContent(
    <Header
      leftContentVisible={leftContentVisible}
      rightContentVisible={rightContentVisible}
      onLayoutChange={handleLayoutChange}
      onOk={handleOk}
    />
  )

  const handleReportChange = (value: string) => {
    setCurrentTemplateId(value)
  }

  const [affixed, setIsAffixed] = React.useState<boolean | undefined>(false)

  const [percentage, setPercentage] = React.useState(0)

  const handleAffixChange = (affixed?: boolean) => {
    setIsAffixed(affixed)
  }

  // TODO:  category
  const handleAnchorClick = (
    e: React.MouseEvent<HTMLElement>,
    link: {
      title: React.ReactNode
      href: string
    }
  ) => {
    e.preventDefault()

    const [tab, href] = compose(
      ([tab, ...rest]: string[]) => [tab, join('-')(rest)],
      split('-'),
      pathOr('', ['href'])
    )(link)

    if (category !== 'overview') {
      handleTabChange(tab)
    }

    if (!href) {
      return
    }

    window.scroll({
      behavior: 'smooth',
      left: 0,
      top: document.getElementById(href)?.offsetTop,
    })
  }

  const handleValuesChange = React.useCallback(
    (_: any, { data }: { data: { [key: string]: any } }) => {
      setPercentage(
        Math.round(
          (compose(length, filter(compose(not, isNil)), values)(data) / total) *
            100
        )
      )
    },
    [total]
  )

  React.useEffect(() => {
    handleValuesChange(null, form.getFieldsValue())
  }, [total]) // eslint-disable-line

  const renderCategory = (category: string) => {
    return compose(gt(__, 0), length, pathOr([], [category]))(snippetList) ? (
      <h2 id={category} className="my-2">
        {category}
      </h2>
    ) : null
  }

  let colSpan = 14 + col(leftContentVisible) + col(rightContentVisible)

  return (
    <ReactDndProvider className="is--preview">
      <BackTop />
      <Page>
        <Form form={form} onValuesChange={handleValuesChange}>
          <Row gutter={16}>
            {leftContentVisible && (
              <Col span={5}>
                <Collapse
                  // accordion
                  defaultActiveKey={[1, 2]}
                  expandIcon={() => null}
                  className="shadow-2xl">
                  <Collapse.Panel
                    header={<div className="text-center"></div>}
                    key={1}>
                    <Row className="mb-2 text-sm text-darkGrey">
                      <Col span={24} className="mb-3">
                        <div className="inline-block" css={{ width: 65 }}>
                          
                        </div>
                        <Form.Item noStyle name="reviewDoctor">
                          <Input className="w-2/3" bordered={false} />
                        </Form.Item>
                      </Col>
                      <Col span={24} className="my-3">
                        <div className="inline-block" css={{ width: 65 }}>
                          
                        </div>
                        <Form.Item noStyle name="serialNo">
                          <Input className="w-2/3" bordered={false} />
                        </Form.Item>
                      </Col>
                      <Col span={24} className="my-3">
                        <div className="inline-block" css={{ width: 65 }}>
                          
                        </div>
                        <Select
                          className="w-2/3"
                          bordered={false}
                          value={templateId}
                          onChange={handleReportChange}>
                          {reportTemplates.map(x => (
                            <Select.Option key={x.id} value={x.id}>
                              {x.name}
                            </Select.Option>
                          ))}
                        </Select>
                      </Col>
                      <Col span={24} className="mt-3">
                        <Affix offsetTop={0} onChange={handleAffixChange}>
                          <Anchor
                            showInkInFixed
                            affix={false}
                            className="is--fixed"
                            style={getAnchorStyles(affixed)}
                            onClick={handleAnchorClick}>
                            {compose(
                              mapIndexed((key: string, idx: number) => (
                                <Link
                                  key={`${key}-${idx}`}
                                  href={key}
                                  title={key}>
                                  {compose(
                                    mapIndexed((x: SnippetProps) => (
                                      <Link
                                        key={x.id}
                                        href={`${key}-${x.id}`}
                                        title={pathOr(
                                          x.id,
                                          ['setting', 'text'],
                                          x
                                        )}
                                      />
                                    )),
                                    pathOr([], [key])
                                  )(snippetList)}
                                </Link>
                              )),
                              keys
                            )(snippetList)}
                          </Anchor>
                        </Affix>
                      </Col>
                    </Row>
                  </Collapse.Panel>
                  <Collapse.Panel
                    header={<div className="text-center"></div>}
                    key={2}>
                    <Row>
                      <Col span={24}>
                        <SnippetListMenu />
                      </Col>
                    </Row>
                  </Collapse.Panel>
                </Collapse>
              </Col>
            )}

            <Col span={colSpan}>
              <div className="pt-2 bg-white rounded-lg shadow-2xl">
                <Affix offsetTop={0}>
                  <div
                    className={cx(
                      'px-4 mb-4 -mt-2',
                      'flex items-center justify-between',
                      'rounded-lg shadow-sm',
                      'text-xl text-darkGrey font-medium'
                    )}
                    css={{ background: '#fff' }}>
                    <div>
                      Pathological Image Report
                      <div className="text-base text-gray-600">
                        Imaging Report From for Hepatocellular Carcinoma
                      </div>
                    </div>
                    <Pie
                      percent={percentage}
                      total={`${percentage}%`}
                      style={{ width: 70, paddingTop: 6 }}
                      height={80}
                    />
                  </div>
                </Affix>
              </div>

              <div className="rounded-lg shadow-2xl">
                <Tabs
                  type="editable-card"
                  activeKey={category}
                  onChange={handleTabChange}
                  onEdit={handleTabEdit}
                  tabBarGutter={8}
                  moveTabNode={handleTabMove}
                  className="flex-1">
                  {compose(
                    map((category: string) => (
                      <Tabs.TabPane
                        closable
                        key={category}
                        tab={
                          <span
                            contentEditable
                            suppressContentEditableWarning
                            spellCheck={false}
                            className="outline-none"
                            css={{ whiteSpace: 'nowrap' }}
                            onChange={handleTabNameChange(category)}>
                            {category}
                          </span>
                        }>
                        <Col
                          span={24}
                          className="p-4 report-template-builder__main-content">
                          <SnippetsContainer
                            // draggable={!isPreview}
                            draggable
                            snippetList={snippetList[category]}
                            onSnippetUpdate={handleSnippetUpdate}
                          />
                        </Col>
                      </Tabs.TabPane>
                    )),
                    keys
                  )(snippetList)}
                  <Tabs.TabPane key="overview" tab="Overview">
                    {compose(
                      map((category: string) => (
                        <div key={category} className="px-3 bg-white">
                          {renderCategory(category)}
                          {compose(
                            renderSnippets({ className: 'mb-0' }),
                            pathOr([], [category])
                          )(snippetList)}
                        </div>
                      )),
                      keys
                    )(snippetList)}
                  </Tabs.TabPane>
                </Tabs>
              </div>
            </Col>

            {rightContentVisible && (
              <Col span={5}>
                <Collapse
                  activeKey={[1, 2, 3, 4]}
                  expandIcon={() => null}
                  className="shadow-2xl">
                  <Collapse.Panel
                    header={<div className="text-center"></div>}
                    key={1}>
                    <div className="border-b border-gray-600 p-3">
                      {data?.ORREQNO}
                    </div>
                    <div className="border-b border-gray-600 p-3">
                      {data?.ORWORKNO}
                    </div>
                    <div className="border-b border-gray-600 p-3">
                      {data?.HNAMEC}
                    </div>
                    <div className="border-b border-gray-600 p-3">
                      {data?.HSEX}
                    </div>
                    <div className="border-b border-gray-600 p-3">
                      {data?.CSECT}
                    </div>
                    <div className="border-b border-gray-600 p-3">
                      {data?.HHISNUM}
                    </div>
                    <div className="border-b border-gray-600 p-3">
                      {data?.HBED}
                    </div>
                    <div className="border-b border-gray-600 p-3">
                      {data?.HBIRTHDT}
                    </div>
                    <div className="border-b border-gray-600 p-3">
                      {data?.ORPROCED}
                    </div>
                    <div className="border-b border-gray-600 p-3">
                      {'LoginUser'}
                    </div>
                    <div className="border-b border-gray-600 p-3">
                      {props.reviewDoctor}
                    </div>
                  </Collapse.Panel>
                  <Collapse.Panel
                    header={<div className="text-center"></div>}
                    key={2}>
                    <Form.Item noStyle name="state">
                      <Radio.Group className="pl-3">
                        <Radio className="block" value="1">
                          
                        </Radio>
                        <Radio className="block mt-1" value="2">
                          
                        </Radio>
                        <Radio className="block mt-1" value="3">
                          
                        </Radio>
                      </Radio.Group>
                    </Form.Item>
                  </Collapse.Panel>
                  <Collapse.Panel
                    header={<div className="text-center"></div>}
                    key={3}>
                    <Form.Item noStyle name="annotations">
                      <CheckboxGroup>
                        {reportAnnotations.map((x, idx) => (
                          <Checkbox
                            key={`annotations-${idx}`}
                            className={cx('block ml-0 pl-2', {
                              'mt-1': idx > 0,
                            })}
                            defaultChecked={false}
                            value={`${idx}`}>
                            {x}
                          </Checkbox>
                        ))}
                      </CheckboxGroup>
                    </Form.Item>
                  </Collapse.Panel>
                  <Collapse.Panel
                    header={<div className="text-center"></div>}
                    key={4}>
                    <div className="pl-6">
                      <code className="bg-yellow-400 px-1">CTRLW</code>,{' '}
                      <code className="bg-yellow-400 px-1">CTRL+S</code> 
                    </div>
                    <div className="pl-6 pt-2">
                      <code className="bg-yellow-400 px-1">F9</code> 
                    </div>
                  </Collapse.Panel>
                </Collapse>
              </Col>
            )}
          </Row>
        </Form>
      </Page>
    </ReactDndProvider>
  )
}

function getAnchorStyles(affixed?: boolean): React.CSSProperties | undefined {
  if (!affixed) {
    return undefined
  }

  return {
    marginLeft: -16,
    marginRight: -16,
    background: '#fff',
    padding: 24,
    borderBottomLeftRadius: 8,
    borderBottomRightRadius: 8,
  }
}
