import React from 'react'
import { atom, useRecoilState } from 'recoil'
import Store from 'store'
import { setComponent } from '@/components/snippet-template-list'
// const { compose } = require('ramda')

export const reportListState = atom({
  key: 'reportListState',
  default: setComponent(Store.get('reportListState') || []),
})

// 
export function useReportList() {
  return useRecoilState(reportListState)
}

export function useReportItem(id: string) {
  const [reportList] = useRecoilState(reportListState)

  const [data, setData] = React.useState<any>()

  React.useEffect(() => {
    setData(reportList.find((x: any) => x.id === id))
  }, [id]) // eslint-disable-line

  return data
}
